<template>
  <div class="main-layout">
    <Sidebar />
    <transition :name="transitionName">
      <router-view class="fullscreen content" />
    </transition>
  </div>
</template>

<script>
import Sidebar from "@/components/Sidebar.vue";

export default {
  props: {
    meta: Object
  },
  data: () => ({
    index: 0,
    transitionName: "slide-left"
  }),
  mounted() {
    this.index = this.meta.index || 0;
  },
  watch: {
    $route(to) {
      const new_index = to.meta.index;
      this.transitionName = new_index < this.index ? "back" : "next";

      this.index = new_index;
    }
  },
  components: {
    Sidebar
  }
};
</script>
